.button {
    width: 132px;
    height: 30px;
    border-radius: 3px;
    padding: 5px 24px;
    font-size: 14px;
    transition: all 0.3s ease;
    border: none;
}

.disabled {
    background: #004d99;
    color: #002e5c;
    opacity: 0.5;
}

.secondary {
    border: 1px solid #06c;
    border-radius: 3px;
    background-color: #fff;
    color: #06c;
}

.default {
    background: #06c;
    color: white;
}

.red {
    background: #cc1439;
    color: white;
}

.default:hover {
    background: #0080ff;
}

.red:hover {
    background: #ff1a47;
}

.secondary:hover {
    background: #e5f0fa;
}

.default:active {
    background: #0059b2;
}

.secondary:active {
    background: #c9e5ff;
}

.red:active {
    background: #b21232;
}

.button:disabled {
    background: #004d99;
    opacity: 0.5;
    cursor: not-allowed;
}